/* HEADER & TOP MENU */

.header {
  width: 88%;
  padding: 1% 6%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9998;
}

.menuLogo {
  display: flex;
  flex-direction: row;
  flex-grow: 5;
}

.menu {
  display: flex;
  flex-direction: row;
  flex-grow: 2;
  justify-content: space-between;
  align-items: center;
}

a.menuItem {
  display: flex;
  font-family: 'Rubik', sans-serif;
  font-size: var(--text-small);
  text-decoration: none;
  color: #ffffff !important;
}

.headerButton {
  display: flex;
  background: rgb(224, 14, 60);
  padding: 0.2rem 0.5rem;
  border-radius: 0.2rem;
}

.headerButton:hover {
  background: rgb(255, 0, 55);
}

a.headerButton {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  text-decoration: none;
  color: #ffffff;
}

a.headerButton:hover {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  text-decoration: none;
  color: #ffffff;
}

a.menuItem:hover {
  color: #919191;
}

button.menuItem {
  border: none;
  background-color: var(--almost-black);
}

button.menuItem:focus {
  outline: none;
}

/* HERO */

.hero {
